html {
  box-sizing: border-box;
}


html, html a {
    /*text-shadow: 1px 1px 1px rgba(0,0,0,0.004);*/
	text-rendering: optimizeLegibility !important;
    -webkit-font-smoothing: antialiased !important;

}
*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  
}

li{
  list-style: none;
}
img {
  max-width: 100%;
}

a {
	color: inherit;
  text-decoration: none;
  &:hover{
	  text-decoration: underline;
  }
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1200px;
}
.footer-container{
  margin: 0 auto;
  
  max-width: 1420px;
}
.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.active{
	font-weight: 900;
  &::after{
    position: absolute;
    content: "";
    width: 80%;
    height: 2px;
    background: #fff;
    left: 10%;
    top: -11px;
  }
  &::before{
    position: absolute;
    content: "";
    width: 80%;
    height: 2px;
    background: #fff;
    left: 10%;
    bottom: -9px;
  }
}
.toHide{
  display: none;
}
.header__content-main-info{
	position: relative;
}
.header__input{
  background: transparent;
  border:none;
  outline: none;
  color:#fff;
  cursor: pointer;
  background-image: url(../img/search.svg);
  background-repeat: no-repeat;
  background-position: center right;
  padding: 0 20px 0 10px;
  width: 10px;
  position: static;
  right: 0;
  top: 0;
  
 
  
}
.aaa{
	height: 0px;
	overflow: hidden;  
	&.active{
		height: auto;  
		overflow: visible;  
	}
	
}




.project__tab-content{
	height: 0px;
	overflow: hidden;  
	&.active{
		height: auto;  
		overflow: visible;  
	}
	
}
.header__input-active{
	animation-name: stretchLeft;
    -webkit-animation-name: stretchLeft;    
 
    animation-duration: 1s; 
    -webkit-animation-duration: 1s;
 
    animation-timing-function: ease-out;    
    -webkit-animation-timing-function: ease-out;    
 
    transform-origin: 100% 0%;
    -ms-transform-origin: 100% 0%;
    -webkit-transform-origin: 100% 0%;


	
	width: 100%;
	margin-right: 96px;
	border-bottom: 1px solid #fff;
}
$white: #ececec;
$black: #111111;
$red1: #000b34;
$red2: #03daee;
$colorPrimary: $red1;
$colorSecondary: $red2;
$cubic-bezier: cubic-bezier(0.68, -0.55, 0.265, 1.55);

//split button mixin
@mixin btn__split($foregroundColor, $backgroundColor, $textColor) {
	button.hover__btn{
    
		text-transform: uppercase;
		text-decoration: none;
		font-weight: 700;
		border: 0;
		//display: block;
		position: relative;
		letter-spacing: 0.15em;
		margin: 0 auto;
		
		background: transparent;
		outline: none;
		font-size: 14px;
		color: $textColor;
		transition: all 0.5s $cubic-bezier 0.15s;
		&::after,
		&::before {
			border: 0;
			content: "";
			position: absolute;
			height: 10px;
			width: 10px;
			transition: all 0.5s $cubic-bezier;
			z-index: -2;
			border-radius: 100%;
			//animation: grow 1s infinite;
		}
		&::before {
			border: 0;
			background-color: $backgroundColor;
			top: 4px;
			left: 0px;
			animation: topAnimation 2s $cubic-bezier 0.25s infinite alternate;
		}
		&::after {
			background-color: $foregroundColor;
			top: 50px;
			left: 200px;
			animation: bottomAnimation 2s $cubic-bezier 0.5s infinite alternate;
		}
		&:hover {
			color: white;
			&::before,
			&::after {
				top: 0;
				//transform: skewx(-10deg);
				height: 100%;
				width: 100%;
				border-radius: 0;
				animation: none;
			}
			&::after {
				left: 0rem;
			}
			&::before {
				top: 0.5rem;
				left: 0.35rem;
			}
		}
	}
}
@keyframes topAnimation {
	from {
		transform: translate(0rem, 0);
	}
	to {
		transform: translate(0rem, 3.5rem);
	}
}
@keyframes bottomAnimation {
	from {
		transform: translate(-11.5rem, 0);
	}
	to {
		transform: translate(0rem, 0);
	}
}

@include btn__split($colorPrimary, $colorSecondary, $black);

.resources__block-item{
