.t2025__wrapper{
	display: flex;
    margin-top: -89px;
    justify-content: flex-end;
}
.t2025-container{
    margin-right: -20px;
    margin: 0 auto;
    
    padding-left: 150px;
    max-width: 1200px

}
.t2025{
    margin-top: -175px;
    position: relative;
    &::after{
        content: "";
        width: 100%;

    }
}
.t2025__line{
    display: none;
    img{
        position: static;
        margin-left: 477px;
        margin-bottom: -6px;
    
    }
}
.t2025__block__number{
    border-top: 2px solid #03DAEE;
    padding-top: 237px;
    position: relative;
    &::before{
        content: "";
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background: #03DAEE;
        position: absolute;
        top: -2px;
        transform: translateY(-50%);
        left: 0;
    }
    &::after{
        content: "";
        width: 2000px;
        height: 2px;
        left: 353px;
        background: #03DAEE;
        position: absolute;
        top: -1px;
        transform: translateY(-50%);
        
    }
}
.t2025__block__text{
    
    padding-top: 109px;
    width: 100%;
}
.advantages--t2025{
    margin-top: 0 !important;
}
.t2025__block__number-item{
    
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 900;
    font-size: 56px;
    line-height: 56px;
    color: #002760;
    text-align: right;
    padding-right: 39px;
    position: relative;
    &::after{
        content: "";
        width: 18px;
        height: 18px;
        border-radius: 50%;
        background: #03DAEE;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: -10px;
    }
    &--2{
        margin-bottom: 200px;
    }
    &--3{
        margin-bottom: 248px;
    }
    &--4{
        margin-bottom: 175px;
    }
    &--5{
        margin-bottom: 118px;
    }
    &--6{
        margin-bottom: 113px;
    }
    &--7{
        margin-bottom: 102px;
    }
}
.t2025__block__number{
    border-right: 2px solid #03DAEE;
}
.t2025__block__text-item{
    margin-bottom: 40px;
    font-family: 'Roboto';
font-style: normal;
font-weight: normal;
font-size: 18px;
line-height: 25px;
/* or 139% */

max-width: 609px;
padding-left: 35px;
/* Secondary */

color: #000;
span{
    color: #002760;
    font-weight: bold;
}

}
.t2025__block__number-item--1{
    margin-bottom: 220px;
}
@media (max-width: 1600px){
    .advantages__arrow{
        right: 241px !important;
    }
}
@media (max-width: 1300px){
    .advantages__arrow{
        right: 92px !important;
    }
}
.footer{
padding: 40px 0;
}
.mission__tumen-2025{
    margin-top: -40px;
    margin-bottom: -220px;
}
