
.header{
  background: rgba(0, 11, 52, 0.3);
  padding: 30px 0 30px 0;
  position: absolute;
  z-index: 1000;
  width: 100%;
  top: 0;
}
.header-bg{
  background:#000B34;
}
.header__wrapper{
  display: flex;
  
  
}
.header__content{
  margin-left: 36px;
  display: flex;
  
}
.header__content-info{
  margin-right: 100px;
  max-width: 321px;
  font-weight: bold;
  font-size: 18px;
  line-height: 21px;
  display: flex;
  color:#fff;
  letter-spacing: 0.2em;
}
.header__content-phone{
  font-weight: bold;
  font-size: 18px;
  
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: #FFFFFF;
  margin-right: 8px;
  span{
    font-weight: normal;
    font-size: 14px;
    line-height: 12px;
    letter-spacing: 0.2em;
    text-transform: capitalize;

  }
  a{
    color: inherit;
    &:hover{
      text-decoration: underline;
    }
  }
}
.header__content-main-info{
  display: flex;
  margin-top: 16px;
  align-items: flex-end;
}
.header__content-language{
  display: flex;
  span{
    font-weight: 900;
    font-size: 14px;
    letter-spacing: 0.2em;
    text-transform: capitalize;
    color: #FFFFFF;
  }
}
.header__content-language-item{
  font-weight: 900;
  font-size: 14px;
  letter-spacing: 0.2em;
  text-transform: capitalize;
  color: #FFFFFF;

  &.active{
    position: relative;
    &::after{
      content: "";
      position: absolute;
      width: 100%;
      height: 2px;
      background: #fff;
      top: -4px;
      left: 0;
  }

  &::before{
      content: "";
      position: absolute;
      width: 100%;
      height: 2px;
      background: #fff;
      bottom: -4px;
      left: 0;
  }
  }
}
.language{
  position: relative;
  &:hover{
    text-decoration: none;
    &::after{
      content: "";
      position: absolute;
      width: 100%;
      height: 2px;
      background: #fff;
      top: -3px;
      left: 0;
  }

  &::before{
      content: "";
      position: absolute;
      width: 100%;
      height: 2px;
      background: #fff;
      bottom: -4px;
      left: 0;
  }
  }
}
.header__content-search{
  margin-left: 11px;
}
.header__main-menu{
  margin-top: 41px;
  display: flex;
  width: 100%;
  align-items: flex-end;
  
}
.header__menu-block{
  display: flex;
}
.header__main-menu-link{
  margin-right: 49.4px;
  position: relative;
  font-size: 12px;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: #FFFFFF;
  cursor: pointer;
  transition: all .8s linear;
  &:last-child{
    margin-right: 0;
  }
  
  &:hover{
    &::after{
      position: absolute;
      content: "";
      width: 80%;
      height: 2px;
      background: #fff;
      left: 10%;
      top: -11px;
    }
    &::before{
      position: absolute;
      content: "";
      width: 80%;
      height: 2px;
      background: #fff;
      left: 10%;
      bottom: -9px;
    }
  }

}